import { useMemo, useCallback } from 'react';
import { useSelector } from 'react-redux';
import {
  monthlyUrlGeneratorSelector,
  monthlyCsvUrlGeneratorSelector,
} from '../selectors';
import { useYearMonths } from '.';

export const useMonthlyUrls2 = (
  generator: (year: number, month: number) => string | undefined
) => {
  const yms = useYearMonths();
  return useMemo(
    () =>
      yms.map(ym => ({
        ...ym,
        url: generator(ym.y, ym.m),
      })),
    [yms, generator]
  );
};

export const useMonthlyUrls = () => {
  const yms = useYearMonths();
  const generateReportUrl = useSelector(monthlyUrlGeneratorSelector);
  const generateCsvUrl = useSelector(monthlyCsvUrlGeneratorSelector);
  return useMemo(
    () =>
      yms.map(ym => ({
        ...ym,
        url: generateReportUrl({ year: ym.y, month: ym.m }),
        csvUrl: generateCsvUrl({
          year: ym.y,
          month: ym.m,
          timeFormat: 'seconds', // TODO
          charset: 'sjis', // TODO
        }),
      })),
    [yms, generateReportUrl]
  );
};

export const useMontylyReportUrls = () => {
  const generateReportUrl = useSelector(monthlyUrlGeneratorSelector);
  return useMonthlyUrls2(
    useCallback(
      (year, month) => generateReportUrl({ year, month }),
      [generateReportUrl]
    )
  );
};
