import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
} from '@mui/material';
import { useYearMonths } from '../hooks';
import { groupBy } from '../utils';
import { urlParamsSlice } from '../slices';
import { AppDispatch } from '../store';
import { useDispatch } from 'react-redux';
import { Fragment } from 'react';

export const MonthSelector: React.FC = () => {
  const yearMonths = Object.entries(groupBy(useYearMonths(), e => e.y)).sort(
    ([a], [b]) => Number(b) - Number(a)
  );
  const dispatch = useDispatch<AppDispatch>();

  return (
    <FormControl>
      <RadioGroup
        row
        onChange={(_, v) => {
          dispatch(urlParamsSlice.actions.setYearMonthByString(v));
        }}
      >
        {yearMonths.map(([y, ym]) => (
          <Fragment key={y}>
            <div style={{ width: '100%' }} />
            {ym.map(({ m }) => (
              <FormControlLabel
                key={m}
                value={`${y}-${m}`}
                control={<Radio />}
                label={`${y}-${m}`}
              />
            ))}
          </Fragment>
        ))}
      </RadioGroup>
    </FormControl>
  );
};

export const MonthSelectorContainer: React.FC = () => <MonthSelector />;
