import { Autocomplete, TextField } from '@mui/material';
import { useDispatch } from 'react-redux';
import { TimecrowdCategory } from '../types';
import { useCategories } from '../hooks';
import { urlParamsSlice } from '../slices';
import { AppDispatch } from '../store';

export const CategorySelector: React.FC<{
  categories: TimecrowdCategory[] | undefined;
}> = ({ categories }) => {
  const dispatch = useDispatch<AppDispatch>();

  return (
    <Autocomplete
      options={categories || []}
      getOptionLabel={option => option.title}
      isOptionEqualToValue={(option, value) =>
        option && value && option.id === value.id
      }
      sx={{ width: 300 }}
      renderInput={params => <TextField {...params} label='Category' />}
      onChange={(_, v) => {
        dispatch(urlParamsSlice.actions.setCategory(v));
      }}
    />
  );
};

export const CategorySelectorContainer = () => {
  const categories = useCategories();
  return <CategorySelector {...{ categories }} />;
};
