import { createSlice } from '@reduxjs/toolkit';
import { PayloadAction } from '@reduxjs/toolkit';
import { TimecrowdCategory } from '../types';

export interface UrlParamsState {
  category?: TimecrowdCategory | null;
  year?: number | null;
  month?: number | null;
}

const initialState: UrlParamsState = {};

export const urlParamsSlice = createSlice({
  name: 'urlParams',
  initialState,
  reducers: {
    setCategory: (
      state,
      action: PayloadAction<TimecrowdCategory | undefined | null>
    ) => {
      state.category = action.payload;
    },
    setYearMonthByString: (state, action: PayloadAction<string>) => {
      const [y, m] = String(action.payload).split('-').map(Number);
      state.year = y;
      state.month = m;
    },
  },
});
