import { useMemo } from 'react';

export const useYearMonths = () =>
  useMemo(() => {
    const y = new Date().getFullYear();
    return Array(3)
      .fill(0)
      .flatMap((_, yDiff) => {
        return Array(12)
          .fill(0)
          .map((_, m) => {
            return { y: y - yDiff, m: m + 1 };
          });
      });
  }, []);
