import {
  FormControl,
  FormLabel,
  RadioGroup,
  Radio,
  FormControlLabel,
} from '@mui/material';
import { AppDispatch } from '../store';
import { useDispatch } from 'react-redux';
import { csvFormatSlice } from '../slices';

export const CsvFormatSelector: React.FC = () => {
  const dispatch = useDispatch<AppDispatch>();

  return (
    <>
      <FormControl>
        <FormLabel>Time format (WIP)</FormLabel>
        <RadioGroup
          row
          defaultValue={csvFormatSlice.getInitialState().timeFormat}
          onChange={(_, v) => {
            dispatch(csvFormatSlice.actions.setTimeFormat(v));
          }}
        >
          <FormControlLabel
            value='seconds'
            control={<Radio />}
            label='seconds'
          />
          <FormControlLabel
            value='hms_colon'
            control={<Radio />}
            label='hms_colon'
          />
        </RadioGroup>
      </FormControl>

      <FormControl>
        <FormLabel>charset (WIP)</FormLabel>
        <RadioGroup
          row
          defaultValue={csvFormatSlice.getInitialState().charset}
          onChange={(_, v) => {
            dispatch(csvFormatSlice.actions.setCharset(v));
          }}
        >
          <FormControlLabel value='sjis' control={<Radio />} label='sjis' />
          <FormControlLabel value='utf8' control={<Radio />} label='utf8' />
          <FormControlLabel
            value='utf8_bom'
            control={<Radio />}
            label='utf8_bom'
          />
        </RadioGroup>
      </FormControl>
    </>
  );
};

export const CsvFormatSelectorContainer: React.FC = () => <CsvFormatSelector />;
