import { teamId } from '../constants';
import { RootState } from '../store';
import { createDraftSafeSelector } from '@reduxjs/toolkit';
import { generateUrl, generateCsvUrl } from '../helpers';

export const urlSelector = createDraftSafeSelector(
  [(s: RootState) => s.urlParams, (s: RootState) => s.timecrowd.users],
  (s, users) =>
    generateUrl({
      categoryId: s.category?.id,
      userIds: users?.map(u => u.id),
      teamId,
    })
);

export const monthlyUrlGeneratorSelector = createDraftSafeSelector(
  [(s: RootState) => s.urlParams, (s: RootState) => s.timecrowd.users],
  (s, users) =>
    ({ year, month }: { year: number; month: number }) =>
      generateUrl({
        categoryId: s.category?.id,
        userIds: users?.map(u => u.id),
        teamId,
        year,
        month,
      })
);

export const monthlyCsvUrlGeneratorSelector = createDraftSafeSelector(
  [(s: RootState) => s.urlParams, (s: RootState) => s.timecrowd.users],
  (s, users) =>
    ({
      charset,
      month,
      timeFormat,
      year,
    }: {
      charset: string;
      month: number;
      timeFormat: string;
      year: number;
    }) =>
      generateCsvUrl({
        categoryId: s.category?.id,
        userIds: users?.map(u => u.id),
        teamId,
        year,
        month,
        charset,
        timeFormat,
      })
);
