const scriptUrl = (
  window.document.querySelectorAll(
    'head script[src^="/static/js/"]'
  )?.[0] as any
)?.src;

export const Usage: React.FC = () => (
  <>
    usage: copy following code and paste to{' '}
    <a href='https://timecrowd.net/app' target='_blank'>
      TimeCrowd
    </a>{' '}
    developer console:
    <div>
      <textarea
        rows={1}
        style={{ width: '100%' }}
      >{`fetch('${scriptUrl}').then(res => res.text()).then(eval);`}</textarea>
    </div>
  </>
);
