const formatDate = (d: Date) =>
  `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(
    d.getDate()
  ).padStart(2, '0')}`;

const dateRange = (
  y: number = new Date().getFullYear(),
  m: number = new Date().getMonth() // last month
) => [formatDate(new Date(y, m - 1, 1)), formatDate(new Date(y, m, 0))];

export const generateUrl = ({
  categoryId,
  userIds,
  teamId,
  year,
  month,
}: {
  categoryId?: number;
  userIds?: number[];
  teamId?: number;
  year?: number | null;
  month?: number | null;
}) => {
  if (!userIds || !categoryId || !teamId) {
    return;
  }
  const userIdQuery = userIds?.map(uid => `user_id=${uid}`).join('&');
  const [from, to] = dateRange(year ?? undefined, month ?? undefined);

  return `https://timecrowd.net/app/report?team_id=${teamId}&category_id=${categoryId}&from=${from}&to=${to}&with_archived=false&${userIdQuery}`;
};

export const generateCsvUrl = ({
  categoryId,
  userIds,
  teamId,
  year,
  month,
  charset,
  timeFormat,
}: {
  categoryId?: number;
  userIds?: number[];
  teamId?: number;
  year?: number | null;
  month?: number | null;
  charset?: string | null;
  timeFormat?: string | null;
}) => {
  if (!userIds || !categoryId || !teamId) {
    return;
  }

  const userIdQuery = userIds?.map(uid => `user_id=${uid}`).join('&');
  const [from, to] = dateRange(year ?? undefined, month ?? undefined);

  return `https://timecrowd.net/teams/${teamId}/report/tasks.csv?charset=${charset}&time_format=${timeFormat}&category_id=${categoryId}&from=${from}&to=${to}&with_archived=false&${userIdQuery}`;
};
