import { useSelector } from 'react-redux';
import { RootState } from '../store';
import { createDraftSafeSelector } from '@reduxjs/toolkit';
import { generateUrl } from '../helpers';
import { teamId } from '../constants';

const linksSelector = createDraftSafeSelector(
  [(s: RootState) => s.urlParams, (s: RootState) => s.timecrowd],
  ({ year, month }, { categories, users }) => {
    if (!categories || !users || !year || !month) {
      return [];
    }
    return categories
      .flatMap(c => (c.children ? [c, ...c.children] : [c]))
      .map(category => ({
        label: category.title,
        href: generateUrl({
          categoryId: category.id,
          userIds: users?.map(u => u.id),
          teamId,
          year,
          month,
        }),
      }))
      .filter((x): x is { label: string; href: string } => !!x.href)
      .sort((a, b) => (a.label.toLowerCase() > b.label.toLowerCase() ? 1 : -1));
  }
);

export const CategoryLinks: React.FC<{
  links: { href: string; label: string }[];
}> = ({ links }) => (
  <div>
    <div>
      {links.map(link => (
        <a
          key={link.href}
          href={link.href}
          target='_blank'
          rel='noreferrer'
          style={{ display: 'inline-block', paddingRight: '.5rem' }}
        >
          {link.label}
        </a>
      ))}
    </div>
    <div>
      <textarea
        rows={6}
        style={{ width: '100%' }}
        defaultValue={links
          .map(link => `${link.href} # ${link.label}`)
          .join('\n')}
      />
    </div>
  </div>
);

export const CategoryLinksContainer: React.FC = () => {
  const links = useSelector(linksSelector);
  return <CategoryLinks {...{ links }} />;
};
