import { createSlice } from '@reduxjs/toolkit';
import { PayloadAction } from '@reduxjs/toolkit';

export interface CsvFormatState {
  timeFormat: string;
  charset: string;
}

const initialState: CsvFormatState = {
  timeFormat: 'hms_colon',
  charset: 'utf8_bom',
};

export const csvFormatSlice = createSlice({
  name: 'csvFormat',
  initialState,
  reducers: {
    setTimeFormat: (state, action: PayloadAction<string>) => {
      state.timeFormat = action.payload;
    },
    setCharset: (state, action: PayloadAction<string>) => {
      state.charset = action.payload;
    },
  },
});
