import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { TimecrowdCategory, User } from '../types';
import { teamId } from '../constants';

export const apiClient = {
  fetch: <T = any>(path: string) =>
    fetch(`/api/${path}`, {
      headers:
        process.env.NODE_ENV === 'development'
          ? {
              authorization: `Bearer ${process.env.REACT_APP_TIMECROWD_ACCESS_TOKEN}`,
            }
          : undefined,
    }).then<T>(res => res.json()),
};

export const fetchUsers = createAsyncThunk('timecrowd/fetchUsersStatus', () =>
  apiClient.fetch<User[]>(`v2/reportable_users?team_id=${teamId}`)
);

export const fetchCategories = createAsyncThunk(
  'timecrowd/fetchCategoriesStatus',
  () => apiClient.fetch<TimecrowdCategory[]>(`v1/teams/${teamId}/categories`)
);

export interface timecrowdState {
  categories?: TimecrowdCategory[];
  users?: User[];
}

const initialState: timecrowdState = {
  categories: JSON.parse(localStorage.getItem('categories') || 'null'),
  users: JSON.parse(localStorage.getItem('users') || 'null'),
};

export const timecrowdSlice = createSlice({
  name: 'timecrowd',
  initialState,
  reducers: {},
  extraReducers: builder => {
    builder.addCase(fetchUsers.fulfilled, (state, action) => {
      state.users = action.payload;
      localStorage.setItem('users', JSON.stringify(state.users));
    });
    builder.addCase(fetchCategories.fulfilled, (state, action) => {
      state.categories = action.payload;
      localStorage.setItem('categories', JSON.stringify(state.categories));
    });
  },
});
