import { useMonthlyUrls } from '../hooks';
import { groupBy } from '../utils';

export const MonthlyLinks: React.FC<{
  urls: { url: string | undefined; y: number; m: number }[] | undefined;
}> = ({ urls }) => (
  <div>
    <div style={{ fontFamily: 'monospace', lineHeight: 1.6 }}>
      {Object.entries(groupBy(urls?.filter(({ url }) => !!url) || [], e => e.y))
        .sort(([a], [b]) => Number(b) - Number(a))
        .map(([y, urls]) => (
          <div key={y}>
            {y}-
            {urls.map(({ url, m }) => (
              <a
                key={`${y}-${m}`}
                href={url}
                target='_blank'
                rel='noreferrer'
                style={{
                  display: 'inline-block',
                  paddingRight: '.5rem',
                }}
              >
                {String(m).padStart(2, '0')}
              </a>
            ))}
          </div>
        ))}
    </div>
    <textarea
      rows={6}
      style={{ width: '100%' }}
      defaultValue={urls
        ?.map(({ url }) => url)
        .filter(url => !!url)
        .join('\n')}
    />
  </div>
);
